.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@font-face {
  font-family: "hylia_serif_betaregular";
  src: url(/zelda_clicky_game/static/media/hyliaserifbeta-regular-webfont.8531d333.woff2) format("woff2"),
    url(/zelda_clicky_game/static/media/hyliaserifbeta-regular-webfont.7ae50c8d.woff) format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  margin: 0;
  padding: 0;
  font-family: "hylia_serif_betaregular", -apple-system, BlinkMacSystemFont,
    "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.gameImg {
  width: 200px;
  height: 200px;
  margin: 1rem;
  object-fit: cover;
  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.5);
}

.gameImg:hover {
  box-shadow: 6px 8px 20px rgba(0, 0, 0, 0.5);
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.modal {
  /* handles the dark transparent background */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  /* handles the modal box with contents */
  position: fixed;
  background: white;
  width: 20%;
  height: auto;
  top: 50%;
  left: 50%;
  padding: 20px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.5);
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

